@import "_palette";

.wrapper {
  display: flex;
  align-self: center;
  flex-direction: column;
  @media screen and (max-width: 475px) {
    justify-content: flex-start;
    align-items: center;
  }
}

.photo {
  width: 200px;
  height: 200px;
  margin: 1rem;
  margin-bottom: 2rem;
  align-self: center;
}

.name {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  font-weight: 600;
  color: $text;
  @media screen and (max-width: 475px) {
    text-align: center;
    font-size: 1.5rem;
  }
}

.description {
  font-size: 1rem;
  text-transform: uppercase;
  color: $primary;
  @media screen and (max-width: 475px) {
    text-align: center;
    font-size: 0.9rem;
    font-weight: bold;
  }
}
